<template>
  <div class="single-item-container">
    <!-- Top Navigation Bar -->
    <nav class="top-nav">
      <div class="nav-logo">
        <img src="logo.png" alt="Logo" />
      </div>
      <div class="nav-icons">
        <span class="profile-icon">{{ getUserInitial }}</span>
        <i class="fas fa-bell notifications-icon"></i>
      </div>
    </nav>

    <!-- Product Details -->
    <div class="product-details" v-if="product">
      <img :src="getImageLink(product.ProductName)" alt="Product Image" class="product-img" />
      <h2>{{ product.ProductName }}</h2>
      <p>Category: {{ product.category }}</p>
      <p>Quantity: {{ product.quantity }}</p>
      <p>Price: ${{ product.price }}</p>
    </div>

    <div v-else>
      <p>Loading product details...</p>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';

export default {
  data() {
    return {
      product: null,
    };
  },
  computed: {
    ...mapState(['products']),
    getUserInitial() {
      return this.$store.getters.currentUser?.name.charAt(0).toUpperCase() || '';
    },
  },
  methods: {
    ...mapActions(['fetchProducts']),
    
    getImageLink(productName) {
      switch (productName) {
        case 'Shampoo':
          return 'https://mapulecodes.github.io/fridayimages/images/shampoo.png';  
        case 'Conditioner':
          return 'https://mapulecodes.github.io/fridayimages/images/conditioner.png';  
        case 'Hair Dryer':
          return 'https://mapulecodes.github.io/fridayimages/images/hairdryer.png';  
        default:
          return 'https://via.placeholder.com/150';  
      }
    }
  },
  mounted() {
    const productId = this.$route.params.id;
    this.fetchProducts().then(() => {
      this.product = this.products.find(p => p.ID === productId);
    });
  }
};
</script>

<style scoped>
/* Styling similar to inventoryView but adjusted for single item */
.single-item-container {
  padding: 20px;
  margin-top: 60px;
}

.product-details {
  text-align: center;
}

.product-img {
  width: 150px;
  height: 150px;
  object-fit: cover;
  margin-bottom: 20px;
}

.top-nav {

}
</style>
